export interface TextareaProps
  extends React.TextareaHTMLAttributes<HTMLTextAreaElement> {
  label?: string;
  bottomRounded?: boolean;
  variant?: "primary" | "secondary";
}

const variants = {
  type: {
    primary:
      "kui-bg-secondary kui-border-[2px] kui-border-black kui-text-white placeholder:kui-text-zinc-400 focus:kui-border-primary",
    secondary:
      "kui-bg-primary kui-border-[2px] kui-border-primary kui-text-black placeholder:kui-text-zinc-700 focus:kui-border-quaternary",
  },
} as const;

const Textarea = (props: TextareaProps) => {
  const {
    label,
    bottomRounded = true,
    variant = "primary",
    ...otherProps
  } = props;

  const baseOnType = variants.type[variant];

  return (
    <div className="kui-relative kui-flex kui-w-full kui-flex-col">
      {label && (
        <p className="kui-pb-2 kui-font-bold kui-text-black">{label}</p>
      )}

      <div className={`kui-relative kui-flex kui-w-full kui-rounded-xl`}>
        <textarea
          className={`
              kui-h-full kui-w-full kui-resize-none 
              kui-appearance-none kui-border-2 kui-p-3
              kui-shadow-inner kui-outline-0 kui-transition-all 
              kui-duration-200 hover:kui-opacity-90 active:kui-opacity-100 
              disabled:kui-cursor-not-allowed disabled:kui-opacity-50
              ${baseOnType}
              ${
                bottomRounded
                  ? "kui-rounded-xl"
                  : "kui-rounded-xl kui-rounded-b-none"
              }
            `}
          {...otherProps}
        />
      </div>
    </div>
  );
};

export default Textarea;
