import { ReactNode } from "react";
import ListItem from "../list-item/ListItem";

export interface CardListItemProps {
  image: string;
  alt: string;
  title: string;
  subtitle?: string;
  icon?: ReactNode;
}

const CardListItem = (props: CardListItemProps) => {
  const { title, subtitle, image, icon, alt } = props;

  return (
    <ListItem
      leftIcon={
        <div className="kui-relative -kui-ml-3 kui-h-12 kui-w-12 kui-overflow-hidden kui-rounded-lg">
          <img alt={alt} src={image} />
        </div>
      }
      rightIcon={icon}
    >
      <div className="kui-flex kui-flex-col kui-space-y-2">
        <span className="kui-text-xs">{title}</span>
        {subtitle && (
          <span className="kui-text-sm kui-font-bold">{subtitle}</span>
        )}
      </div>
    </ListItem>
  );
};

export default CardListItem;
