import React, { ReactNode } from "react";

interface CardProps {
  image: string;
  alt: string;
  icon?: ReactNode;
  title?: string;
  subtitle?: string;
}

const Card: React.FC<CardProps> = ({ title, subtitle, image, icon, alt }) => (
  <div
    className={`kui-flex kui-w-min kui-cursor-pointer kui-flex-col kui-items-center kui-whitespace-nowrap kui-rounded-2xl kui-bg-secondary kui-p-3`}
  >
    <div className="kui-relative kui-h-[116px] kui-w-[116px] kui-overflow-hidden kui-rounded-2xl ">
      <img
        src={image}
        alt={alt}
        className="kui-object-cover"
        width={116}
        height={116}
      />
      {icon && (
        <div className="kui-absolute kui-bottom-1 kui-right-1 ">{icon}</div>
      )}
    </div>

    <div className="kui-mt-4 kui-flex kui-w-full kui-flex-col kui-space-y-2">
      {title && <span className="kui-text-xs kui-text-white">{title}</span>}
      {subtitle && (
        <span className="kui-text-sm kui-font-bold kui-text-white">
          {subtitle}
        </span>
      )}
    </div>
  </div>
);

export default Card;
