import { PropsWithChildren, useContext } from "react";
import { FloatingTabsContext } from "./FloatingTabs";

interface FloatingTabProps extends PropsWithChildren {
  value: number;
}
const FloatingTab: React.FC<FloatingTabProps> = ({ value, children }) => {
  const { value: currentValue, onChange } = useContext(FloatingTabsContext);
  const isCurrent = currentValue === value;
  return (
    <span
      className={`kui-z-50 kui-flex kui-h-full kui-w-6/12 kui-items-center kui-justify-center kui-transition-all kui-duration-200 ${
        !isCurrent ? "kui-text-white" : ""
      }`}
      onClick={() => onChange(value)}
      role="tab"
      aria-selected={isCurrent}
      tabIndex={0}
    >
      {children}
    </span>
  );
};

export default FloatingTab;
