import React, { ReactNode } from "react";

interface ListItemProps {
  rightIcon?: ReactNode;
  leftIcon?: ReactNode;
  children?: ReactNode;
}

const ListItem: React.FC<ListItemProps> = ({
  rightIcon,
  leftIcon,
  children,
}) => {
  return (
    <div className="kui-flex kui-h-[62px] kui-w-full kui-items-center kui-justify-between kui-rounded-2xl kui-bg-secondary kui-px-[18px] kui-text-white">
      {leftIcon && <div className="kui-flex kui-flex-col">{leftIcon}</div>}
      {children && (
        <div className="kui-mx-[25px] kui-flex kui-w-full kui-items-center kui-justify-between">
          {children}
        </div>
      )}
      {rightIcon && rightIcon}
    </div>
  );
};

export default ListItem;
