import { ReactNode } from "react";
import ErrorIcon from "../../icons/error-icon/ErrorIcon";
import SuccessIcon from "../../icons/success-icon/SuccessIcon";

interface InputProps extends React.InputHTMLAttributes<HTMLInputElement> {
  label?: string;
  leftIcon?: ReactNode;
  rightIcon?: ReactNode;
  bottomRounded?: boolean;
  error?: boolean;
  success?: boolean;
}

const Input = (props: InputProps) => {
  const {
    label,
    leftIcon,
    rightIcon,
    bottomRounded = true,
    error = false,
    success = false,
    ...otherProps
  } = props;

  const getInputPaddingX = () => {
    if (leftIcon && rightIcon) {
      return "kui-px-11";
    }
    if (leftIcon) {
      return "kui-pl-11 kui-pr-4";
    }
    if (rightIcon) {
      return "kui-pr-11 kui-pl-4";
    }
    return "kui-px-4";
  };

  return (
    <div className={`kui-relative kui-flex kui-w-full kui-flex-col`}>
      {label && (
        <p className="kui-pb-2 kui-font-bold kui-text-black">{label}</p>
      )}
      <div className={`kui-relative kui-w-full kui-rounded-xl kui-text-white `}>
        {leftIcon && (
          <div className="kui-absolute kui-left-4 kui-z-10 kui-flex kui-h-full kui-items-center">
            {leftIcon}
          </div>
        )}
        {rightIcon && (
          <div className="kui-absolute kui-right-4 kui-z-10 kui-flex kui-h-full kui-items-center">
            {rightIcon}
          </div>
        )}
        {error && (
          <div className="kui-absolute kui-right-4 kui-z-10 kui-flex kui-h-full kui-items-center">
            <ErrorIcon></ErrorIcon>
          </div>
        )}
        {success && (
          <div className="kui-absolute kui-right-4 kui-z-10 kui-flex kui-h-full kui-items-center">
            <SuccessIcon></SuccessIcon>
          </div>
        )}
        <input
          className={`
              kui-h-full kui-w-full kui-border-2 kui-bg-black kui-p-3
              kui-text-white kui-shadow-inner kui-outline-0 kui-transition-all 
              kui-duration-200 placeholder:kui-text-zinc-400 hover:kui-opacity-90 
              focus:kui-border-primary focus:kui-bg-tertiary 
              disabled:kui-cursor-not-allowed disabled:kui-opacity-60
              ${
                bottomRounded
                  ? "kui-rounded-xl"
                  : "kui-rounded-xl kui-rounded-b-none"
              }
              ${
                error
                  ? "kui-border-error focus:kui-border-error"
                  : "kui-border-black"
              } 
              ${
                success
                  ? "kui-border-success focus:kui-border-success"
                  : "kui-border-black"
              }
              ${getInputPaddingX()}
            `}
          {...otherProps}
        />
      </div>
    </div>
  );
};

export default Input;
