import { ReactNode } from "react";

type HTMLButton = React.ButtonHTMLAttributes<HTMLButtonElement>;

export interface ButtonProps extends HTMLButton {
  rightIcon?: ReactNode;
  leftIcon?: ReactNode;
  variant?: "primary" | "secondary";
}

const variants = {
  type: {
    primary:
      "kui-bg-primary kui-border-[2px] kui-border-primary kui-text-black",
    secondary:
      "kui-bg-transparent kui-border-[2px] kui-border-primary kui-text-primary",
  },
} as const;

const Button = (props: ButtonProps) => {
  const { rightIcon, leftIcon, variant = "primary", ...otherProps } = props;

  const baseOnType = variants.type[variant];
  const svgClass =
    variant === "primary"
      ? "kui-button_svg-primary"
      : "kui-button_svg-secondary";

  return (
    <button
      className={`kui-relative kui-h-[42px] kui-w-full kui-rounded-[10px] kui-font-bold hover:kui-opacity-75 active:kui-opacity-100 disabled:kui-cursor-not-allowed disabled:kui-opacity-50 lg:kui-h-[58px] lg:kui-text-[20px] ${baseOnType}  `}
      {...otherProps}
    >
      {leftIcon && (
        <div
          className={`kui-button_svg kui-absolute kui-top-1/2 kui-left-[20px] kui-flex -kui-translate-y-1/2 kui-items-center kui-justify-center ${svgClass}`}
        >
          {leftIcon}
        </div>
      )}
      {otherProps.children}
      {rightIcon && (
        <div
          className={`kui-button_svg kui-absolute kui-top-1/2 kui-right-[20px] -kui-translate-y-1/2 kui-items-center kui-justify-center ${svgClass}`}
        >
          {rightIcon}
        </div>
      )}
    </button>
  );
};

export default Button;
