import React, {
  Children,
  createContext,
  PropsWithChildren,
  useState,
} from "react";

interface FloatingTabsProps extends PropsWithChildren {
  value: number;
  onChange: (value: number) => void;
}

export const FloatingTabsContext = createContext({
  value: 0,
  onChange: (value: number) => {},
});

const FloatingTabs: React.FC<FloatingTabsProps> = ({
  value,
  onChange,
  children,
}) => {
  const count = Children.count(children);
  return (
    <FloatingTabsContext.Provider value={{ value, onChange }}>
      <div
        className="kui-relative kui-flex kui-min-h-[2.5rem] kui-cursor-pointer kui-items-center kui-rounded-[50px] kui-border-2 kui-border-primary kui-bg-transparent kui-font-bold kui-text-secondary"
        role="tablist"
      >
        {children}

        <div
          className={`kui-absolute kui-z-40 kui-flex kui-h-full kui-items-center kui-justify-center kui-rounded-[50px] kui-bg-primary kui-transition-all kui-duration-200`}
          style={{
            width: `${100 / count}%`,
            left: `${(100 / count) * value}%`,
            border: "3px solid transparent",
            backgroundClip: "padding-box",
          }}
        />
      </div>
    </FloatingTabsContext.Provider>
  );
};

export default FloatingTabs;
