import { PropsWithChildren, useContext } from "react";
import { TabsContext } from "./Tabs";

interface TabProps extends PropsWithChildren {
  value: number;
}

const Tab: React.FC<TabProps> = ({ value, children }) => {
  const { value: currentValue, onChange } = useContext(TabsContext);
  const isCurrent = currentValue === value;
  return (
    <div
      className={`${
        isCurrent ? "kui-bg-secondary" : "kui-bg-tertiary"
      } kui-bg-darker kui-cursor kui-relative kui-flex kui-w-full kui-cursor-pointer kui-flex-col kui-items-center kui-justify-center kui-py-3 kui-transition-colors kui-duration-200`}
      onClick={() => onChange(value)}
      role="tab"
      aria-selected={isCurrent}
      tabIndex={0}
    >
      <div
        className={`kui-flex kui-flex-row kui-items-center kui-justify-center ${
          isCurrent ? "kui-text-primary" : "kui-text-white"
        }`}
      >
        {children}
        <div
          className={`${
            isCurrent ? "kui-bg-primary" : ""
          } kui-absolute kui-bottom-0 kui-h-[3.5px] kui-w-full`}
        />
      </div>
    </div>
  );
};

export default Tab;
